import React, { Component } from 'react';
import Particles from '../components/particles/Particles.js';
import Draw from '../components/draw/Draw.js';
import Setting from "../components/settings/Settings.js";
import Popup from '../components/settingsPopup/Popup.js'


export class Intro extends Component {

    state = {
        popupState: false,
        option: window.localStorage.getItem("skPort"),
    }

    toggleSetting = (e) => {
        this.setState({popupState: !this.state.popupState})
    }

    changeTheme = (e) => {
        let theme = window.localStorage;
        this.setState({option: e.target.dataset.bg})
        theme.removeItem("skPort");
        theme.setItem("skPort", e.target.dataset.bg);
    }

    render() {
        const { popupState, option } = this.state
        
        return (
            <div className="position-relative">
                <Particles theme={option}/>
                <Draw theme={option}/>
                <Setting toggleEvent={this.toggleSetting} />
                {popupState ? <Popup changeEvent={this.changeTheme} toggleEvent={this.toggleSetting}/> : null}
            </div>
        )
    }
}

export default Intro
