import React, { Component } from 'react'

export class Github extends Component {

    state = {
        repos: [],
        page: 1,
    }

    componentDidMount(){
        const observerSetting = {
            root: null,
            rootMargin: "0px",
            threshold: 0
        }

        const observer = new IntersectionObserver( entry => {
            if(entry[0].isIntersecting){
                const url = `https://api.github.com/users/simk395/repos?per_page=15&sort=updated&page=${this.state.page}`;
                this.setState({page: this.state.page + 1})
                this.fetchRepos(url)
            }
        }, observerSetting)
            
        const loadEl = document.querySelector("#loading");
        observer.observe(loadEl)
    }

    fetchRepos = async (url) => {
        const header = {
            "Accept": "application/vnd.github.v3+json",
            "Authorization": `token ${process.env.REACT_APP_API_KEY}` 
            };

        try{ 
            const req = await fetch(url, header);
            const res = await req.json();
            if(res.message || res === []){
                return;
            }
            await this.setState({repos: [...this.state.repos, ...res]})
        }catch(e){
            throw new Error("403 Rate Limit Exceeded.");
        }
    }

    render() {
        const { repos:data } = this.state
        return (
            <div>
                <h1 className="text-dark">Simk395</h1>
                <ul className="list-group-flush overflow-hidden">
                    {
                        data.map(repo => !document.querySelector("#chk").checked ?
                            <li className="list-group-item bg-light observer"><a className="fs-2 text-dark" href={repo.html_url} target="_blank">{repo.name}</a></li>
                            :
                            <li className="list-group-item bg-dark observer"><a className="fs-2 text-light" href={repo.html_url} target="_blank">{repo.name}</a></li>
                            )
                    }
                    <span id="loading" style={{visibility: "hidden"}}>loading...</span>
                </ul>
            </div>
        )
    }
}

export default Github
