import Intro from './container/Intro.js';
import Home from './container/Home.js'
import {Switch, Route, Redirect} from "react-router-dom";

function App() {
  
  function checkVisited(){
    let theme = window.localStorage;
    let page = <Home/>
    if(!theme.getItem("skPort")){
      theme.setItem("skPort", "Food");
      page = <Redirect to="/intro"/>
    }
    return page;
  }
  
   return (
    <div>
      <Switch>
          <Route exact path="/intro" component={Intro}/>
          <Route exact path="/">
            {checkVisited}
          </Route>
        </Switch>
    </div>
  );
}

export default App;
