import React, { Component } from 'react';
import './popup.css';

export class Popup extends Component {
    render() {
        const {toggleEvent, changeEvent} = this.props;
        return (
            <div className="center bg-dark d-flex flex-column h-50 w-25 settings-popup">
                <div className="list-group settings-popup-header">
                    <ul className="d-flex flex-column justify-content-around p-0 m-0 h-100 settings-popup-body" onClick={changeEvent}>
                        <li className="h-100 list-group-item d-flex justify-content-center bg-food pointer" data-bg="Food"><p className="align-self-center">Food</p></li>
                        <li className="h-100 list-group-item bg-dark d-flex justify-content-center bg-gaming pointer" data-bg="Gaming"><p className="align-self-center">Gaming</p></li>
                        <li className="h-100 list-group-item bg-dark d-flex justify-content-center bg-cats pointer" data-bg="Cats"><p className="align-self-center">Cats</p></li>
                    </ul>
                </div>
                <div className="d-flex justify-content-end flex-grow-1 settings-popup-footer">
                    <button className="popup-btn" onClick={toggleEvent}>Close</button>
                </div>
                
            </div>
        )
    }
}

export default Popup
