import React, { Component } from 'react'
import Cats from "./themes/Cats";
import Food from "./themes/Food";
import Gaming from "./themes/Gaming";

export class Particles extends Component {
    render() {
        const { theme } = this.props;
        let option;
        switch(theme){
            case "Food":
                option = <Food/>
                break;  
            case "Gaming":
                option = <Gaming/>
                break;
            case "Cats":
                option = <Cats/>;
                break;
            default:
                break;  
        }

        return (
            <div>
                {option}
            </div>
        )
    }
}

export default Particles
