import React, { Component } from 'react';
import Effect from 'react-particles-js';

export class Cats extends Component {
    render() {
        return (
            <div>
                <Effect
                    height="100%"
                    width="100%"  
                    className="vh-100 vw-100 bg-orange"
                    params={{
                    "particles": {
                        "number": {
                        "value": 10,
                        "density": {
                            "enable": true,
                            "value_area": 800
                            }
                        },
                        "shape": {
                        "type": "images",
                        "image": [
                            {src: './cat1.png', height: 10, width:10},
                            {src: './cat2.png', height: 10, width:10},
                            {src: './cat3.png', height: 10, width:10},
                            {src: './cat4.png', height: 10, width:10}
                        ]
                        },
                        "opacity": {
                        "value": 0.75,
                        "random": false,
                        "anim": {
                            "enable": false,
                            "speed": 1,
                            "opacity_min": 0.1,
                            "sync": false
                        }
                        },
                        "size": {
                        "value": 40,
                        "random": true,
                        "anim": {
                            "enable": true,
                            "speed": 0,
                            "size_min": 40,
                            "sync": false
                        }
                        },
                        "line_linked": {
                        "enable": false,
                        "distance": 150,
                        "color": "#FF0000",
                        "opacity": 0.4,
                        "width": 1
                        },
                        "move": {
                        "enable": true,
                        "speed": 10,
                        "direction": "none",
                        "random": true,
                        "straight": false,
                        "out_mode": "out",
                        "attract": {
                            "enable": false,
                            "rotateX": 600,
                            "rotateY": 1200
                        }
                        }
                    },
                    "interactivity": {
                        "detect_on": "canvas",
                        "events": {
                        "onhover": {
                            "enable": false,
                            "mode": "repulse"
                        },
                        "onclick": {
                            "enable": false,
                            "mode": "push"
                        },
                        "resize": false
                        },
                        "modes": {
                        "grab": {
                            "distance": 400,
                            "line_linked": {
                            "opacity": 1
                            }
                        },
                        "bubble": {
                            "distance": 400,
                            "size": 40,
                            "duration": 2,
                            "opacity": 8,
                            "speed": 3
                        },
                        "repulse": {
                            "distance": 200
                        },
                        "push": {
                            "particles_nb": 4
                        },
                        "remove": {
                            "particles_nb": 2
                        }
                        }
                    },
                }}/>
            </div>
        )
    }
}

export default Cats
