import React, { Component } from 'react';
import Vara from 'vara';
import 'bootstrap/dist/css/bootstrap.min.css';
import { withRouter } from 'react-router-dom';

export class Draw extends Component {

    constructor(props){
        super(props);
        this.myRef = React.createRef();
    }

    componentDidMount(){
        const { theme } = this.props;

        let color1, color2;

        switch(theme){
            case "Food":
                color1 = "#000000";
                color2 = "#FBD428";
                break;
            case "Gaming":
                color1 = "#FFFFFF";
                color2 = "#000000";
                break;
            case "Cats":
                color1 = "#283350";
                color2 = "#FFB500";
                break;
            default:
                break;
        }

        new Vara(".draw", "https://raw.githubusercontent.com/akzhy/Vara/master/fonts/Pacifico/PacificoSLO.json", [{
            text: "Simon Kong",
            fontSize: 85,
            color: color1,
            y: 10,
            
        }, {
            text: "Learn. Design. Code.",
            fontSize: 40,
            color: color2,
            y: -100
        }], {
            strokeWidth: 2,
            textAlign:"center"
            });

        
        let btn = document.createElement("button");
        btn.innerText = "Enter";
        btn.className = "btn btn-success btn-lg";
        btn.addEventListener("click", this.redirectHome)
        this.myRef.current.append(btn);
    }

    redirectHome = (e) => {
        let history = this.props.history;
        history.push("/")
    }

    render() {
        
        return (
            <div className="bg-clear center draw" ref={this.myRef}/>
        )
    }
}

export default withRouter(Draw);
