import React, { Component } from 'react'
import './nav.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMoon, faSun } from '@fortawesome/free-solid-svg-icons'


export class Nav extends Component {
    
    render() {
        const { changeTheme } = this.props;
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-white">
            <a className="navbar-brand mr-auto" href="#">Simon Kong (Work In Progress)</a>
 
            <div className="one-quarter" id="switch">
                <input type="checkbox" className="checkbox" id="chk" onChange={changeTheme}/>
                <label className="label" for="chk">
                    <FontAwesomeIcon className="fa-moon" icon={faMoon} />  
                    <FontAwesomeIcon className="fa-sun" icon={faSun} />
                    <div className="ball"></div>
                </label>
            </div>
            
          </nav>
        )
    }
}

export default Nav
