import React, { Component } from 'react';
import Nav from '../components/nav/Nav.js';
import Sidenav from '../components/sideNav/Sidenav.js';
import Projects from '../components/projects/Projects.js';

export class Home extends Component {

    state = {
        dark: false
    }
    
    componentDidMount(){
        if(window.localStorage.getItem("skPortTheme")){
            document.querySelector("#chk").checked = true;
            this.changeTheme();
        }
    }

    componentDidUpdate(){
        this.state.dark ? this.changeToDark() : this.changeToLight()
    }

    //toggle theme between light and dark
    changeTheme = () => {
        this.setState({dark: !this.state.dark})
    };
    
    //change theme to dark
    changeToDark = () => {
        window.localStorage.setItem("skPortTheme", "dark");
        const lightEle = document.querySelectorAll(".bg-light");
        const whiteEle = document.querySelectorAll(".bg-white");
        const blackTextEle = document.querySelectorAll(".text-dark");
        const navEle = document.querySelector("nav");

        //change projects color to dark
        lightEle.forEach(ele => {
            ele.classList.remove("bg-light");
            ele.classList.add("bg-dark");
            
        })
       
        //change nav color to black
        whiteEle.forEach(ele => {
            ele.classList.remove("bg-white");
            ele.classList.add("bg-black");
        })

        //change text color to white
        blackTextEle.forEach(ele => {
            ele.classList.remove("text-dark");
            ele.classList.add("text-light");
        })

        //change nav to light text
        navEle.classList.remove("navbar-light");
        navEle.classList.add("navbar-dark");
    }

    //change theme to light
    changeToLight = () => {
        window.localStorage.removeItem("skPortTheme");
        const darkEle = document.querySelectorAll(".bg-dark");
        const blackEle = document.querySelectorAll(".bg-black");
        const whiteTextEle = document.querySelectorAll(".text-light");
        const navEle = document.querySelector("nav");

         //change projects color to light
         darkEle.forEach(ele => {
            ele.classList.remove("bg-dark");
            ele.classList.add("bg-light");
            
        })
       
        //change nav color to white
        blackEle.forEach(ele => {
            ele.classList.remove("bg-black");
            ele.classList.add("bg-white");
        })

        //change text color to dark
        whiteTextEle.forEach(ele => {
            ele.classList.remove("text-light");
            ele.classList.add("text-dark");
        })

        //change nav to dark text
        navEle.classList.remove("navbar-dark");
        navEle.classList.add("navbar-light");
    }

    render() {
        return (
            <div>
                <Nav changeTheme={this.changeTheme}/>
                <div className="d-flex h-wo-nav">
                    <Sidenav />
                    <Projects />
                </div>
            </div>
        )
    }
}

export default Home
