import React, { Component } from 'react'
import "./settings.css"


export class Settings extends Component {
    render() {
        return (
            <div id="setting">
                <input 
                    id="setting-image" 
                    className="nav-icon" 
                    type="image" 
                    src="settings.png" 
                    alt="Settings" 
                    onClick={this.props.toggleEvent}
                />
            </div>
        )
    }
}

export default Settings
